___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --background-color: #1a1a2e;
  --container-color: #16213e;
  --primary-color: #0f3460;
  --accent-color: #e94560;
  --text-color: #e0e0e0;
  --input-background: #0f3460;
  --input-border: #1a1a2e;
  --button-background: #e94560;
  --button-hover-background: #d81f45;
  --button-text-color: #fff;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  background-color: var(--container-color);
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  text-align: center;
  width: 100%;
  max-width: 400px;
}

h1 {
  font-size: 2.2em;
  font-weight: 500;
  color: var(--accent-color);
  margin-bottom: 20px;
}

.generated-password {
  font-size: 1.6em;
  padding: 15px;
  background-color: var(--input-background);
  border-radius: 8px;
  margin-bottom: 25px;
  color: var(--text-color);
  border: 1px solid var(--input-border);
}

label {
  display: block;
  text-align: left;
  font-weight: 500;
  margin: 15px 0 5px;
  color: var(--text-color);
}

input[type="number"],
input[type="checkbox"] {
  margin-right: 10px;
}

input[type="number"] {
  width: 100%;
  padding: 12px;
  font-size: 1em;
  border-radius: 8px;
  border: 1px solid var(--input-border);
  background-color: var(--input-background);
  color: var(--text-color);
}

input[type="checkbox"] {
  transform: scale(1.2);
}

.generate-password {
  width: 100%;
  padding: 15px;
  font-size: 1.2em;
  font-weight: 700;
  color: var(--button-text-color);
  background-color: var(--button-background);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
  transition: background-color 0.3s;
}

.generate-password:hover {
  background-color: var(--button-hover-background);
}
